import Main from '@/views/Main';
import type0 from '@/views/type0';

const routes = [
  {
    path: '/main',
    component: Main
  },
  {
    path: '/type0',
    component: type0
  },
  {
    path: '*',
    redirect: '/main'
  }
];

export default routes;



