<template>
  <div class="main">
    <v-parallax
      :style="{ height: screenHig + 'px' }"
      src="https://apertureless.github.io/vue-parallax/static/img/pexels-photo-241307.jpeg"
    >
      <div class="box">
        <p>
          쓸쑤록 반하는.<br />
          GD 서비스<br />
        </p>
        <div class="point" v-for="(k, index) in 10" v-parallax-point="{ speed: (index / 10), y: scrollY, index: 0 }">
          <p class="item"></p>
        </div>
      </div>
    </v-parallax>
    <v-parallax
      :style="{ height: screenHig + 'px' }"
      src="https://cdn.vuetifyjs.com/images/backgrounds/vbanner.jpg"
    >
      <div class="box">
        <p>
          월 1만원으로<br />
          골퍼라면 누구든<br />
          그 이상의 가치를 즐길 수있는<br />
          GD 서비스 혜택
        </p>
        <div class="point" v-for="(k, index) in 10" v-parallax-point="{ speed: (index / 10), y: scrollY, index: 1 }">
          <p class="item"></p>
        </div>
      </div>
    </v-parallax>
    <v-parallax
      :style="{ height: screenHig + 'px' }"
      src="https://apertureless.github.io/vue-parallax/static/img/pexels-photo-241307.jpeg"
    >
      <div class="box">
        <p>
          혜택1.<br />
          월 7회 라운드만 해도 다 돌려드림<br />
          GD 회원권 상당의 모바일 이용권 100% 지급
        </p>
        <div class="point" v-for="(k, index) in 10" v-parallax-point="{ speed: (index / 10), y: scrollY, index: 2 }">
          <p class="item"></p>
        </div>
      </div>
    </v-parallax>
    <v-parallax
      :style="{ height: screenHig + 'px' }"
      src="https://cdn.vuetifyjs.com/images/backgrounds/vbanner.jpg"
    >
      <div class="box">
        <p>
          혜택2.<br />
          스크린 경기를 더욱 즐겁게 힘차게<br />
          약 60 여종의 플러스샵 아이템<br />
          볼 볼꼬리 코스매니저 월 무제한 사용<br />
          내맘대로 자유룝게~
        </p>
        <div class="point" v-for="(k, index) in 10" v-parallax-point="{ speed: (index / 10), y: scrollY, index: 3 }">
          <p class="item"></p>
        </div>
      </div>
    </v-parallax>
    <v-parallax
      :style="{ height: screenHig + 'px' }"
      src="https://apertureless.github.io/vue-parallax/static/img/pexels-photo-241307.jpeg"
    >
      <div class="box">
        <p>
          혜택3.<br />
          투어대회 투어CC입장권 월 0매<br />
          명문 프로대화/CC를 스키린에서<br />
          생생한 현장을 체험해보세요~
        </p>
        <div class="point" v-for="(k, index) in 10" v-parallax-point="{ speed: (index / 10), y: scrollY, index: 4 }">
          <p class="item"></p>
        </div>
      </div>
    </v-parallax>
    <v-parallax
      :style="{ height: screenHig + 'px' }"
      src="https://cdn.vuetifyjs.com/images/backgrounds/vbanner.jpg"
    >
      <div class="box">
        <p>
          혜택4.<br />
          한번도 안가본 사람은 있어도<br />
          한번만 간 사람은 없다!<br />
          필드 나스모 촬영이 가능한 골프존 카운티<br />
          그린피 1만원 월 무제한 할인
        </p>
        <div class="point" v-for="(k, index) in 10" v-parallax-point="{ speed: (index / 10), y: scrollY, index: 5 }">
          <p class="item"></p>
        </div>
      </div>
    </v-parallax>
    <v-parallax
      :style="{ height: screenHig + 'px' }"
      src="https://apertureless.github.io/vue-parallax/static/img/pexels-photo-241307.jpeg"
    >
      <div class="box">
        <p>
          혜택5.<br />
          머리부터 발끝까지 HOT한 신상 용품<br />
          직접 체험하고 구매하자<br />
          골프존 마켓 HOT하게 월 무제한 7% 할인<br /><br />

          GD회원은 묻지도 따지지도 않고<br />
          마켓 자사몰VUP회원으로 즉시 승급!
        </p>
        <div class="point" v-for="(k, index) in 10" v-parallax-point="{ speed: (index / 10), y: scrollY, index: 6 }">
          <p class="item"></p>
        </div>
      </div>
    </v-parallax>
    <v-parallax
      :style="{ height: screenHig + 'px' }"
      src="https://cdn.vuetifyjs.com/images/backgrounds/vbanner.jpg"
    >
      <div class="box">
        <p>
          혜택6.<br />
          특가 각종 이벤트 혜택이 쏟아지는<br />
          필드 부킹 전문 티스케너<br />
          10,000원 할인 쿠폰 월 1매
        </p>
        <div class="point" v-for="(k, index) in 10" v-parallax-point="{ speed: (index / 10), y: scrollY, index: 7 }">
          <p class="item"></p>
        </div>
      </div>
    </v-parallax>
    <v-parallax
      :style="{ height: screenHig + 'px' }"
      src="https://apertureless.github.io/vue-parallax/static/img/pexels-photo-241307.jpeg"
    >
      <div class="box">
        <p>
          GET IT 혜택<br />
          혜택차사들의 열일로 탄생하는<br />
          게릴라 혜택까지<br />
          혜택은 계속 됩니다.
        </p>
      </div>
    </v-parallax>
  </div>
</template>

<script>
import * as VParallax from 'vuetify/es5/components/VParallax';
export default {
  name: 'Main',
  components: {
    ...VParallax
  },
  data () {
    return {
      screenHig: window.innerHeight,
      scrollY: window.pageYOffset || document.documentElement.scrollTop,
      contextIndex: 0,
      translate: 0,
      pintY: 0,
      contentDom: window.document.querySelectorAll('.v-parallax__content')
    };
  },
  methods: {
    handleScroll (event) {
      this.scrollY = window.pageYOffset || document.documentElement.scrollTop;
      this.contextIndex = Math.round(this.scrollY/this.screenHig);
      this.translate = this.scrollY - (this.contextIndex * this.screenHig);
      this.contentDom[this.contextIndex].style.cssText = 'transform: translate(0, ' + this.translate + 'px);';
    }
  },
  created: function () {
    setTimeout(() => {
      this.contentDom = window.document.querySelectorAll('.v-parallax__content');
      window.addEventListener('scroll', this.handleScroll);
    });
  },
  destroyed: function () {
    window.removeEventListener('scroll', this.handleScroll);
  }
};
</script>

<style lang="less">
  .main {
    width:100%;
    text-align:center;
  .v-parallax__content {
    position:relative;
    font-size:20px;
  .box {
    position:absolute;
    bottom:50%;
    left:0;
    width:100%;
    height:50%;
    display:flex;
    vertical-align: bottom;
  p {
    position:relative;
    z-index:1;
    width:100%;
    align-self: flex-end;
  }
  .point {
    position:relative;
    position:absolute;
    background:red;
    border-radius: 50%;
    box-shadow: 0 0 10px red;
    /*-webkit-transition: all 0.3s cubic-bezier(0.25, 0.8, 0.5, 1);*/
  }
  }
  }
  }
</style>






