<template>
  <div class="main">
    <v-parallax
      src="https://apertureless.github.io/vue-parallax/static/img/pexels-photo-241307.jpeg"
    >
      <div class="box">
        <p>
          쓸쑤록 반하는.<br />
          GD 서비스
        </p>
      </div>
    </v-parallax>
    <v-parallax
      src="https://apertureless.github.io/vue-parallax/static/img/pexels-photo-241307.jpeg"
    >
      <div class="box">
        <p>
          월 1만원으로<br />
          골퍼라면 누구든<br />
          그 이상의 가치를 즐길 수있는<br />
          GD 서비스 혜택
        </p>
      </div>
    </v-parallax>
    <v-parallax
      src="https://apertureless.github.io/vue-parallax/static/img/pexels-photo-241307.jpeg"
    >
      <div class="box">
        <p>
          혜택1.<br />
          월 7회 라운드만 해도 다 돌려드림<br />
          GD 회원권 상당의 모바일 이용권 100% 지급
        </p>
      </div>
    </v-parallax>
  </div>
</template>

<script>
import VParallax from '../views/Parallax';
export default {
  name: 'Main',
  components: {
    VParallax
  },
  data () {
    return {
      itemData: [
        {
          src: 'https://apertureless.github.io/vue-parallax/static/img/pexels-photo-241307.jpeg',
          height: 500
        },
        {
          src: 'https://cdn.vuetifyjs.com/images/backgrounds/vbanner.jpg',
          height: 500
        }
      ],
      screenHig: window.innerHeight,
      scrollY: window.pageYOffset || document.documentElement.scrollTop,
      contextIndex: 0,
      contentDom: window.document.querySelectorAll('.v-parallax__content'),
      pointSpeed: function () {
        return (Math.random() * 100)/100;
      }
    };
  },
  methods: {
    handleScroll (event) {
      this.scrollY = window.pageYOffset || document.documentElement.scrollTop;
      this.contextIndex = Math.round(this.scrollY/this.screenHig);
      var translate = this.scrollY - (this.contextIndex * this.screenHig);
      var opc = 1-(Math.abs(translate)/(this.screenHig/2));
      this.contentDom[this.contextIndex].style.cssText = 'transform: translate(0, ' + translate + 'px);';
    }
  },
  created: function () {
    setTimeout(() => {
      this.contentDom = window.document.querySelectorAll('.v-parallax__content');
      // window.addEventListener('scroll', this.handleScroll);
    });
  },
  destroyed: function () {
    window.removeEventListener('scroll', this.handleScroll);
  }
};
</script>

<style lang="less">
  .main {
    width:100%;
    text-align:center;
    .v-parallax__content {
      position:relative;
      font-size:20px;
      .box {
        position:absolute;
        bottom:50%;
        left:0;
        width:100%;
        height:50%;
        display:flex;
        vertical-align: bottom;
        p {
          position:relative;
          z-index:1;
          width:100%;
          align-self: flex-end;
        }
        .point {
          .item {
            position:absolute;
            background:red;
            border-radius: 50%;
            box-shadow: 0 0 10px red;
            -webkit-transition: all 0.3s cubic-bezier(0.25, 0.8, 0.5, 1);
          }
        }
      }
    }
  }
</style>






